.tip {
  font-family: HelveticaNeue-Medium;
  font-size: 14px;
  color: #1a1a1a;
  letter-spacing: 0;
  margin-top: 23px;
  text-align: left;
  display: flex;
  justify-content: space-between;
  margin-bottom: 6px;

  .stakePopTitle {
    margin-bottom: 0;
  }

  .stakeAll {
    background: #3d56d6;
    border-radius: 10px;
    font-family: PingFangSC-Regular;
    font-size: 10px;
    transform: scale(0.833);
    transform-origin: right;
    color: #ffffff;
    letter-spacing: 0;
    width: calc(36px / 0.833);
    height: calc(18px / 0.833);
    line-height: calc(18px / 0.833);
    text-align: center;
    cursor: pointer;

    &.gray {
      background: #e2d9f6;
      color: #b79cef;
      cursor: not-allowed;
    }
  }
}

