/* claim style */
.claim {
  width: 81.4%;
  margin: 40px auto 0;

  .collect-title {
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #333333;
    letter-spacing: 0;
  }

  .collect-amount {
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #666666;
    letter-spacing: 0;
    text-align: left;
    margin-top: 4px;
    padding: 0 16px;

    background: rgba(170, 170, 170, 0.2);
    border-radius: 10px;
    height: 50px;
    line-height: 50px;
  }

  .min-btn {
    background: #3d56d6;
    border-radius: 10px;
    font-family: PingFangSC-Regular;
    font-size: 10px;
    transform: scale(0.833);
    transform-origin: right;
    color: #ffffff;
    letter-spacing: 0;
    width: calc(36px / 0.833);
    height: calc(18px / 0.833);
    line-height: calc(18px / 0.833);
    text-align: center;
    cursor: pointer;

    &.gray {
      background: #e2d9f6;
      color: #b79cef;
      cursor: not-allowed;
    }
  }

  .unlocked {
    margin-top: 23px;
  }

  .claimInput {
    margin-top: 6px;
    border: 1px solid #3d56d6;
    box-shadow: 0 20px 40px 0 rgba(148, 148, 148, 0.06);
    border-radius: 10px;
    width: 100%;
    padding: 0 16px;
    letter-spacing: 0;
    text-align: center;
    height: 50px;
    line-height: 50px;

    input {
      background: none;
      font-family: PingFangSC-Regular;
      font-size: 14px;
      color: #222222;
      letter-spacing: 0;
    }
  }

  .collect-unlock-max {
    margin-top: 10px;
    text-align: left;

    span {
      font-family: HelveticaNeue;
      font-size: 12px;
      color: #999;
      letter-spacing: 0;
      line-height: 17px;

      &.collenct-unlock-amount {
        color: #151515;
      }
    }
  }
}

