.pool-tabs {
  .tabs-fixed {
    &.tabs-mobile {
      .ant-tabs-nav {
        top: 55px;
      }
    }

    .ant-tabs-nav {
      width: 1200px;
      position: fixed;
      top: 0;
      z-index: 10;
      background-color: #f9f9fd;
    }

    .ant-tabs-content-holder {
      margin-top: 52px;
    }
  }

  &.bottom-border {
    .ant-tabs-nav-wrap {
      overflow: visible;
    }
  }
}

.pool-tabs {
  margin-top: 35px;
  margin-bottom: 120px;

  .ant-tabs-tab {
    padding-bottom: 2px;
  }

  .ant-tabs-top > .ant-tabs-nav::before,
  .ant-tabs-bottom > .ant-tabs-nav::before,
  .ant-tabs-top > div > .ant-tabs-nav::before,
  .ant-tabs-bottom > div > .ant-tabs-nav::before {
    border-bottom: none;
  }

  .ant-tabs,
  .ant-tabs-tab {
    font-family: PingFangSC-Medium;
    font-size: 18px;

    &:nth-child(2) {
      margin-right: 6px;
    }

    .pools-swap {
      margin-right: 20px;
    }

    .pools-swap-other {
      margin: 0 20px;
    }

    .pools-lend {
      margin-left: 20px;
    }
  }

  .ant-tabs-tab.ant-tabs-tab-active .ant-tabs-tab-btn,
  .ant-tabs-tab:hover,
  .ant-tabs-tab-btn:hover {
    color: #3d56d6;
  }

  .ant-tabs-top > .ant-tabs-nav .ant-tabs-ink-bar,
  .ant-tabs-top > div > .ant-tabs-nav .ant-tabs-ink-bar {
    display: none;
  }

  .ant-tabs-nav {
    margin-bottom: 4px;
  }

  .ant-badge {
    font-size: 18px;
  }

  .ant-badge-count,
  .ant-badge-dot,
  .ant-badge .ant-scroll-number-custom-component {
    right: -3px;
    top: 3px;
  }

  .ant-tabs-tab:nth-child(1):after {
    content: close-quote;
    height: 18px;
    width: 2px;
    color: black;
    border-radius: 5px;
    position: absolute;
    top: 0.1em;
    height: 18px;
    right: -16px;
    top: 17px;
    content: '';
    width: 1px;
    border-left: solid #e7e7e7 2px;
  }

  .ant-tabs.multi-tab {
    .ant-tabs-nav-list {
      display: flex;
      justify-content: space-between;
      width: 100%;
    }

    .ant-tabs-tab:nth-child(1):after {
      display: none;
    }
  }

  &.bottom-border {
    .ant-tabs-nav {
      border-bottom: 1px solid #ebe5f0;
    }
  }
}

.token-modal {
  background: url(../images/token-modal-bg.png) 0 0 no-repeat;
  background-size: 100% 100%;
  border-radius: 17px;

  .ant-modal-content {
    background: transparent;
  }

  .ant-modal-body {
    padding-bottom: 60px;
  }

  .token-title {
    font-family: PingFang SC;
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    color: #fff;
    text-align: center;
    margin-top: 14px;
  }

  .token-sub-title {
    font-family: PingFang SC;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    margin: 6px auto 20px;

    .value {
      color: #101010;
    }
  }

  .token-content {
    margin: 0 22px;
    flex-wrap: wrap;

    .token-list {
      width: calc((100% - 20px * 2) / 3);
      height: 32px;
      line-height: 32px;
      margin-bottom: 14px;
      padding: 0 10px;
      background: #f7f7f7;
      border-radius: 10px;

      .token-name {
        font-family: Helvetica Neue;
        font-size: 12px;
        color: #1a1a1a;
        align-items: center;

        > img {
          width: 16px;
          height: 16px;
          margin-right: 6px;
        }
      }

      .token-value {
        font-family: Helvetica Neue;
        font-size: 12px;
        color: #1ba27a;
      }
    }
  }

  &.new-token-modal {
    background-image: none;
    background-color: #fff;
    padding-bottom: 0;

    .token-title {
      color: #595959;
      margin-bottom: 30px;
    }

    .claim-detail {
      margin-top: 15px;
    }
  }
}

.pools-tab {
  font-family: PingFang SC;

  &.pools-swap,
  &.pools-swap-other,
  &.pools-lend {
    font-size: 14px;
    line-height: 20px;
    padding: 12px 0;
    color: rgba(26, 26, 26, 0.4);

    &.current {
      position: relative;
      color: #1a1a1a;

      &::after {
        content: '';
        display: block;
        width: 100%;
        height: 1px;
        background-color: #1a1a1a;
        position: absolute;
        left: 0;
        bottom: 6px;
      }
    }
  }

  &.outdate {
    font-size: 14px;
    line-height: 22px;
    color: #999;

    > span {
      color: rgba(26, 26, 26, 0.4);
    }
  }

  .ant-tabs-tab {
    padding: 0;
  }
}

.ant-tabs-tab-active .outdate > span {
  color: #1a1a1a;
}

.mt20 {
  margin-top: 20px;
}
