html,
body,
* {
  font-size: 12px;
}

.pool-container {
  margin: 0;
  overflow: hidden;
  height: 100vh;
  min-height: 780px;

  &.sunold-container-old {
    width: 80%;
  }

  .banner-title {
    z-index: 1;
    width: 100%;
    text-align: center;
    margin-top: 100px;

    .banner-main-title {
      width: 414px;
      height: 70px;
    }

    .banner-main-title-text {
      font-family: PingFang SC;
      font-style: normal;
      font-weight: 600;
      font-size: 40px;
      line-height: 70px;
      letter-spacing: 1px;
      color: #ffffff;
      margin: 0;
    }

    .banner-sub-title {
      font-style: normal;
      font-weight: 300;
      font-size: 18px;
      line-height: 25px;
      text-align: center;
      color: #ffffff;

      span {
        font-style: normal;
        font-weight: 300;
        font-size: 18px;
        line-height: 25px;
        text-align: center;
        color: #ffffff;
      }

      a {
        color: #fff;
        font-size: 18px;
        text-decoration: underline;
      }
    }
  }

  .pool-bg {
    background-image: url(../images/JSTPoolBg.png);
    background-size: 100% 100%;
    position: absolute;
    top: 20px;
    bottom: 20px;
    left: 20px;
    right: 20px;
    margin: auto;
    overflow: hidden;

    .pool-bg-left {
      background-image: url(../images/poolBgLeft.png);
      width: 201px;
      height: 119px;
      left: 160px;
      top: 255px;
      position: absolute;
      background-size: contain;
    }

    .pool-bg-right {
      background-image: url(../images/poolBgRight.png);
      width: 508px;
      height: 493px;
      right: 0;
      bottom: 0;
      position: absolute;
    }
  }

  .multiple-claimed-cards {
    margin-top: 25px;

    .multiple-claimed-card {
      margin: 0 auto;
      max-width: 500px;

      .container .container-title > span > span > span {
        font-size: 12px;
        color: rgba(153, 153, 153, 0.6);
        margin-left: 6px;
      }
    }
  }

  .footer-container {
    color: #fff;

    a {
      color: #fff;
    }
  }

  .ant-modal-wrap {
    left: 194px;
  }

  .ant-alert.ant-alert-closable {
    // position: absolute;
    // top: 260px;
    // left: 0;
    // right: 0;
    margin: 0 auto;
    padding-right: 30px;
    // width: 76%;
    margin: 20px auto 0;
    font-size: 14px;
  }

  .ant-alert.alert-new {
    text-align: left;
    padding-left: 30px;
    padding-right: 40px;
    background: #f8e1cd;
    border: none;
    border-radius: 5px;
    margin-top: 20px;

    .ant-alert-message {
      color: #f5820d;
      font-size: 12px;

      > span > b {
        display: inline-block;
        width: 12px;
        height: 12px;
        margin-right: 6px;
        background: url(../images/warning.svg) 0 0 no-repeat;
        background-size: 100%;
        transform: translateY(1px);
      }
    }

    .ant-alert-close-icon {
      right: 24px;
    }

    .anticon-close {
      display: inline-block;
      width: 14px;
      height: 14px;
      background: url(../images/warning-close.svg) 0 0 no-repeat;
      background-size: 100%;

      > svg {
        display: none;
      }
    }
  }

  .ant-alert.alert-old {
    position: absolute;
    top: 260px;
    left: 0;
    right: 0;
    margin: 0 auto;
    padding-right: 30px;
    width: 61%;
    margin: 20px auto 0;
    font-size: 14px;
  }

  .ant-alert-message {
    color: rgba(255, 0, 0, 0.85);
  }

  .intro {
    padding-top: 50px;
  }

  .intro-old {
    height: auto;

    h1,
    h4 {
      color: #fff;
      font-weight: 400;
    }

    h1 {
      font-family: HelveticaNeue-CondensedBlack;
      font-size: 50px;
      letter-spacing: 0.93px;
      line-height: 52px;
      margin-bottom: 20px;
      text-shadow: none;
    }

    h4 {
      font-family: HelveticaNeue-Bold;
      font-size: 18px;
      letter-spacing: 0;
      margin-bottom: 10px;
    }

    p,
    p a {
      color: #fff;
      font-size: 12px;
      background: none;
    }
  }

  .sunold-dashboard {
    // width: 76%;
    margin: 0 auto;
    border-radius: 15px;
    text-align: left;
    background: #ffffff;
    box-shadow: 0 20px 40px 0 rgba(148, 148, 148, 0.06);
  }

  .sunold-dashboard-old {
    width: 76%;
    margin-top: 80px;
  }

  .stake-content {
    padding: 10px 30px 10px;
    border-top: 1px solid rgb(89, 21, 225, 0.1);
  }

  .sunold-nav {
    // width: 76%;
    // margin: 80px auto 0;
    // display: flex;
    // align-items: center;
    padding: 26px 30px;
    // justify-content: space-between;
    color: #1f1f1f;

    // border-radius: 15px;
    .title {
      font-family: PingFangSC-Regular;
      font-size: 14px;
      color: #666666;
      letter-spacing: 0;
      // padding-left: 30px;
      // background: url(../images/stakeLogo.svg) left no-repeat;
      // background-size: 20px 20px;
    }

    .mined_amount {
      margin-top: 6px;
      font-family: PingFangSC-Semibold;
      font-size: 30px;
      color: #333333;
      letter-spacing: 0.8px;

      span {
        font-family: PingFangSC-Semibold;
        font-size: 30px;
        color: #333333;
        letter-spacing: 0.8px;

        &.trxUni {
          font-size: 24px;
