.home-title {
  text-align: center;
  margin: 160px 0 50px;
  font-family: HelveticaNeue-Bold;
  font-size: 26px;
  color: #1f1f1f;
  letter-spacing: 0;
}

.pool-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  position: relative;

  &.old {
    margin-top: 12px;
  }

  &.fromGenesis {
    justify-content: center;
  }

  &.fromHome {
    margin-top: 0;
  }

  .announcement {
    font-family: PingFang SC;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 17px;
    text-align: left;
    color: #666;
    margin-top: -24px;

    a {
      font-size: 12px;
      text-decoration: underline;
      color: #3d56d6;

      &:hover {
        color: #3d56d6;
      }
    }
  }

  .coming {
    display: block;
    width: 100%;
    margin: 120px 0 100px;

    .title {
      margin-top: 30px;
      font-family: PingFangSC-Regular;
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 17px;
      text-align: center;
      color: #333333;
    }

    .tip {
      font-family: PingFangSC-Regular;
      font-style: normal;
      font-weight: normal;
      font-size: 12px;
      line-height: 14px;
      text-align: center;
      color: #999999;
      margin-top: 4px;

      a {
        font-family: PingFangSC-Regular;
        font-size: 12px;
        color: #3d56d6;
        text-decoration: underline;
      }
    }

    img {
      width: 14%;
    }
  }

  .heightSet {
    height: 122px;
  }

  .heightSetForGenesis {
    height: 98px;

    &.notLogin {
      height: 192px;
    }
  }

  .bottomFix {
    width: 100%;
  }

  .tip_unlock {
    font-size: 16px;
    color: #3d56d6;
    cursor: not-allowed;
    margin: 0 5px;
    position: relative;

    &.mini {
      cursor: pointer;
    }

    span {
      font-size: 12px;
    }

    a {
      color: #3d56d6;
      font-size: 12px;
      line-height: 20px;
    }

    &.gray {
      color: #b79cef;
    }

    &.mini {
      font-size: 12px;
      color: #999;
      text-align: right;

      &.mr {
        margin-right: 9.75%;
      }
    }

    .fr {
      position: absolute;
      right: 30px;
      top: 5px;
      font-size: 12px;
      color: #999;
      text-align: right;
    }
  }
  .sunold {
    width: 100%;
    width: calc((100% - 30px * 2) / 3);
    padding: 20px;
    background: #ffffff;
    box-shadow: 0 20px 40px 0 rgba(148, 148, 148, 0.06);
    border-radius: 15px;
    margin: 10px 20px 10px 0;
    position: relative;
    overflow: hidden;
    height: 470px;

    .bottomFix {
      position: absolute;
      bottom: 20px;
      width: calc(100% - 40px);

      .type-link.gray {
        cursor: not-allowed;
        color: #b79cef;
      }

      .create_btn.gray {
        cursor: not-allowed;
      }
    }

    .sunold-header,
    .ge-title {
      display: flex;
      align-items: center;
      justify-content: flex-start;
    }

    .ge-title {
      .title {
        margin-left: 10px;
      }
    }

    .ant-statistic {
      display: inline-block;

      .ant-statistic-content-value {
        color: #fff;
        font-size: 14px;
      }
    }

    a.type-link {
      color: #3d56d6;
      font-size: 12px;
      line-height: 20px;
      display: block;
      margin-top: 25px;

      &.preview {
        margin-top: 6px;
      }
    }

    .over {
      transform: rotate(-315deg);
      transform-origin: right top;
      font-family: AvenirNext-Bold;
      font-size: 14px;
      color: #ffffff;
      background: #999;
      position: absolute;
      right: -25px;
      top: 80px;
      height: 33px;
      line-height: 33px;
      width: 150px;

      &.preview {
        background: #27b78c;
        border-radius: 8px;
      }
    }

    img.tokenLogo {
      width: 14%;
    }

    img.sunoldLogo {
      width: 14%;
      margin-left: -10px;
    }

    .header-right {
      text-align: left;
      margin-left: 10px;
    }

    .title {
      font-family: HelveticaNeue;
      font-size: 20px;
      // font-size: 1.4vw;
      color: #1a1a1a;
      letter-spacing: 0;
      margin-bottom: 0;

      &.en {
        font-size: 1.3vw;
      }
    }

    .hr-sub {
      display: flex;
      align-items: center;
      transform: scale(0.833);
      transform-origin: left;
    }

    .tip {
      font-family: HelveticaNeue;
      font-size: 0.8vw;
      color: #999999;
      letter-spacing: 0;
      text-align: center;
      display: inline-block;
      margin-top: 0px;

      &.ml2 {
        margin-left: 5px;
      }

      b {
        font-weight: 600;
        color: #3d56d6;
      }
    }

    .total {
      margin: 30px auto 0;
      text-align: left;
      display: flex;
      justify-content: space-between;
      transform: scale(0.9);
      transform-origin: left;
      width: 110%;

      &.countdown {
        font-family: HelveticaNeue;
        font-size: 14px;
        letter-spacing: 0;
        line-height: 16px;
        justify-content: center;

        &.red {
          color: #d90019;
        }

        &.green {
          color: #1ba27a;
          margin-top: 50px;
        }
      }

      &.hide {
        opacity: 0;
      }

      .t-num {
        font-size: 12px;
      }

      .apy {
        font-family: HelveticaNeue;
        font-size: 13px;
        color: #1ba27a;
        letter-spacing: 0;
      }
    }

    .sunold-claim {
      .claim-fast {
        cursor: pointer;
        color: #3d56d6;
        text-decoration: underline;
      }
    }

    .htSet {
      height: 56px;

      &.preview {
        height: 30px;
      }
    }

    .info {
      margin: 10px auto 0px;
      padding: 14px 20px;
      width: 100%;
      position: relative;

      background: #ffffff;
      box-shadow: inset 0 -2px 8px 0 rgba(51, 1, 138, 0.08);
      border-radius: 15px;

      div {
        text-align: left;

        p {
          margin-bottom: 8px;
        }

        p:nth-child(1) {
          font-family: HelveticaNeue;
          font-size: 10px;
          color: #666666;
          line-height: 18px;
          letter-spacing: 0;
          transform: scale(0.83);
          margin-left: -9.2%;
        }

        p:nth-child(n + 2) {
          font-family: HelveticaNeue;
          font-size: 14px;
          color: #1a1a1a;
          letter-spacing: 0;
          display: flex;
          justify-content: space-between;

          span:nth-child(2) {
            font-family: HelveticaNeue;
            font-size: 12px;
            color: #999999;
            letter-spacing: 0;
          }
        }

        &:last-child {
          p:last-child {
            margin-bottom: 0;
          }
        }
      }

      &.mt1 {
        margin: 10px auto 0;

        div p:nth-child(2) {
          font-family: HelveticaNeue-Medium;
          font-size: 18px;
          color: #3d56d6;
          letter-spacing: 0;
        }
      }

      &.left {
        box-shadow: none;
      }

      .preview_info {
        div {
          text-align: center;
        }

        .preview_title {
          font-family: PingFangSC-Regular;
          font-size: 16px;
          color: #1ba27a;
          letter-spacing: 0;
          line-height: 22px;
          margin-top: 6px;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }

        .preview_total {
          font-family: PingFangSC-Regular;
          font-size: 10px;
          color: #666666;
          letter-spacing: 0;
          margin-top: 16px;
          text-align: left;
          margin-left: calc((100% - 123px) / 2);

          .preview_amount {
            margin-left: 4px;
            font-family: HelveticaNeue;
            font-size: 14px;
            color: #1a1a1a;
            letter-spacing: 0;
          }

          &.day {
            margin: 8px 0 8px;
            text-align: left;
            margin-left: calc((100% - 123px) / 2);
          }
        }
      }

      usd {
        color: #3d56d6;
      }
    }

    .create_btn {
      background: #3d56d6;
      border-radius: 10px;
      width: 100%;
      height: 40px;
      line-height: 40px;
      font-family: HelveticaNeue;
      font-size: 12px;
      color: #ffffff;
      letter-spacing: 0;
      text-align: center;
      display: block;
      margin: 13px auto 0;

      .ant-statistic-content {
        font-size: unset;
      }

      .ant-statistic-content-value {
        color: #b79cef;
        font-size: 12px;
      }

      &.gray {
        cursor: auto;
        background: #e2d9f6;
        border-radius: 10px;
        box-shadow: none;
        font-family: HelveticaNeue;
        font-size: 12px;
        color: #b79cef;
        letter-spacing: 0;
        text-align: center;
      }
    }

    .staked {
      width: 81.5%;
      text-align: left;
      margin: 40px auto 0;

      .tip {
        font-family: HelveticaNeue;
        font-size: 12px;
        color: #999999;
        letter-spacing: 0;
      }

      .btnStyle {
        width: 100%;
        margin: 6px auto 0;
        background: #e2d9f6;
        border-radius: 15px;
        font-family: HelveticaNeue-Medium;
        font-size: 14px;
        color: #3d56d6;
        letter-spacing: 0;
        height: 58px;
        line-height: 58px;
        padding: 0 20px;
      }
    }
  }
}
